
import axios from 'axios'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default({
data(){
return {
userAccount:'...',
nodeAccount:'...',
proAccount:'...'
}
},
created(){
this.getAccount()
},
methods:{
getAccount(){
var self=this;
axios.all([
axios.get('https://mp.orancat.com/puser/getUserAccount'),
axios.get('https://mp.orancat.com/puser/getPnodeAccount'),
axios.get('https://mp.orancat.com/puser/getProductAccount')
])
.then(axios.spread(function (res1,res2,res3) {
self.userAccount=res1.data.return
self.nodeAccount=res2.data.return
self.proAccount=res3.data.return
}));
}

},
components: {
XHeader,
Cell
}
})



