
import {getRecomment,getDiscList} from 'api/recomment'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import axios from 'axios'
export default {
data(){
return {
tabIndex:0,
orderInfo:[],
statusTag: ['', '待付款', '待发货', '已发货', '已完成']
}

},
created(){
this.getOrList(0)
},
methods:{
onTabClick(index){
console.log(index)
this.tabIndex=index;
this.getOrList(this.tabIndex);
},
clickItem(orderId){




this.$router.push({path:`/order/`+orderId})
},
getOrList(queryKey){
this.$vux.loading.show({
text: 'Loading'
})

axios({
method:'POST',
url:'https://mp.orancat.com/oshop/getorderlist',
data:{
queryKey:queryKey
},
headers:{
"token": "1531798043804ORANMALLEELL"
}
}).then((res)=>{
this.$vux.loading.hide()
console.log(res.data,'getorderlist')
if(res.data.status==0){
this.orderInfo=res.data.orderdoc;
}else{
this.$vux.alert.show({
title:'提示',
content:res.data.message
})
}
})
}
},
watch: {
'$route' (to, from) {
if(from.name=='ordtl'){
this.getOrList(0)
}
}
},
components: {
Tab,
TabItem
}
}



