export default {
   data() {
     return {
       account:0
     }
   },
   created(){
     let cartlistData=this.$store.state.cartList.goodsGroug
     let accountPrice=0
     Object.keys(cartlistData).forEach(item=>{
       accountPrice+=cartlistData[item].data.price*cartlistData[item].data.num
     })
     this.account=accountPrice
   },
   methods:{
     cancel(){
       this.$router.go(-1)
     }
   }
}



