
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import axios from 'axios'
export default {
data(){
return {
value:'',
orderInfo:null,
orderId:'',
productId:'',
name:'',
phone:'',
detail:'',
wxId:'',
creactDate:'',
orderState:'',
orderStateNum:100,
kuaidi:'',
mark:'',
sendNum:'',
statusTag: ['', '待付款', '待发货', '已发货', '已完成']
}

},
created(){
this.getOrderDtl(this.$route.params.id)
},
methods:{
getOrderDtl(orderId){
this.$vux.loading.show({
text: 'Loading'
})

axios({
method:'Get',
url:'https://mp.orancat.com/oorder/getDtl?orderId='+orderId,
}).then((res)=>{
this.$vux.loading.hide()
console.log(res.data,'getorderlist')
if(res.data.status==0){
this.orderInfo=res.data.orderInfo;
this.orderId=this.orderInfo.orderId;
this.name=this.orderInfo.addressInfo.name;
this.phone=this.orderInfo.addressInfo.phone;
this.detail=this.orderInfo.addressInfo.detail;
this.wxId=this.orderInfo.addressInfo.wxId?this.orderInfo.addressInfo.wxId:'';
this.creactDate=this.format(this.orderInfo.creactDate);
this.orderState=this.statusTag[this.orderInfo.orderState];
this.orderStateNum=this.orderInfo.orderState;
}else{
this.$vux.alert.show({
title:'提示',
content:res.data.message
})
}
})
},
hasGetMoney(){
var self=this;
this.$vux.loading.show({
text: 'Loading'
})

axios({
method:'POST',
url:'https://mp.orancat.com/oorder/pconfirm',
data:{
orderId:this.orderId,
mark:this.mark
},
headers:{
"token": cookie.get('token')
}
}).then((res)=>{
this.$vux.loading.hide()
if(res.data.status==0){
this.$vux.alert.show({
title:'提示',
content:'确认收款成功',
onHide () {
self.$router.push({name: 'order',params:{ reload:'1'}});
}
})
}else{
this.$vux.alert.show({
title:'提示',
content:res.data.message
})
}
})
},
hasSend(){
var self=this;
if(this.sendNum==''){
this.$vux.alert.show({
title:'提示',
content:'确认先输入快递单号'
})
return false
}
this.$vux.loading.show({
text: 'Loading'
})

axios({
method:'POST',
url:'https://mp.orancat.com/oorder/stateToSend',
data:{
orderId:this.orderId,
mark:this.mark,
sendNum:this.sendNum
},
headers:{
"token": cookie.get('token')
}
}).then((res)=>{
this.$vux.loading.hide()
if(res.data.status==0){
this.$vux.alert.show({
title:'提示',
content:'确认发货成功',
onHide () {
self.$router.push({name: 'order',params:{ reload:'1'}});
}
})
}else{
this.$vux.alert.show({
title:'提示',
content:res.data.message
})
}
})
},
format(datestr){
var shijianchuo = parseInt(datestr);
var time = new Date(shijianchuo);
var y = time.getFullYear();
var m = time.getMonth() + 1;
var d = time.getDate();
var h = time.getHours();
var mm = time.getMinutes();
var s = time.getSeconds();
return y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s);
},
add0(m) { return m < 10 ? '0' + m : m }
},



components: {
XHeader,
XInput,
XButton
}
}



