import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import catalogDtl from '@/components/catalogDtl/catalogDtl'
import payPage from '@/components/payPage/payPage'

import formDemo from '@/components/formDemo/formDemo'
import login from '@/components/login/login'
import order from '@/components/order/order'
import ordtl from '@/components/order/ordtl/ordtl'
import userCenter from '@/components/userCenter/userCenter'
import message from '@/components/message/message'
import proSayStatistics from '@/components/proSayStatistics/proSayStatistics'

import homePage from '@/components/homePage/homePage'
import goOut from '@/components/goOut/goOut'
import approval from '@/components/approval/approval'
import approvalDtl from '@/components/approvalDtl/approvalDtl'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/homePage'
    },
    {
      path: '/homePage',
      name: 'homePage',
      component: homePage,
      meta:{auth:true}
    },
    {
      path: '/goOut',
      name: 'goOut',
      component: goOut
    },
    {
      path: '/approval',
      name: 'approval',
      component: approval,
      children:[
        {
          path:':id',
          name:'approvalDtl',
          component:approvalDtl	
        }
      ]
    },
    {
      path: '/userCenter',
      name: 'userCenter',
      component: userCenter,
      meta:{auth:true}
    },
    {
      path: '/order',
      name: 'order',
      component: order,
      meta:{auth:true},
      children:[
        {
          path:':id',
          name:'ordtl',
          component:ordtl	
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/message',
      name: 'message',
      component: message,
      meta:{auth:true}
    },
    {
      path: '/proSayStatistics',
      name: 'proSayStatistics',
      component: proSayStatistics,
      meta:{auth:true}
    },
    {
      path: '/formDemo',
      name: 'formDemo',
      component: formDemo
    }

  ]
})



