
import axios from 'axios'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'


export default({
data(){
return {
messageList:[]
}
},
created(){
this.getMessageList()
},
methods:{
getMessageList(){
this.$vux.loading.show({
text: 'Loading'
})
var self=this;
axios({
method:'GET',
url:'https://mp.orancat.com/omessage/message',
}).then((res)=>{
this.$vux.loading.hide()
console.log(res.data,'getMessageList')
if(res.data.status==0){

if(res.data.messageList.length>0){
res.data.messageList.forEach(function(item,index){
item.creatdate=self.format(item.creatdate)
})
self.messageList=res.data.messageList
}

}else{
this.$vux.alert.show({
title:'提示',
content:res.data.message
})
}
})
},
format(datestr){
var shijianchuo = parseInt(datestr);
var time = new Date(shijianchuo);
var y = time.getFullYear();
var m = time.getMonth() + 1;
var d = time.getDate();
var h = time.getHours();
var mm = time.getMinutes();
var s = time.getSeconds();
return y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s);
},
add0(m) { return m < 10 ? '0' + m : m }

},
components: {
XHeader,
Divider
}
})



