
import {mapMutations,mapGetters} from 'vuex'
import XButton from 'vux/src/components/x-button/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {getRecomment,getDiscList} from 'api/recomment'
import axios from 'axios'
export default {
data(){
return {
userImg:'',
sname:'',
wxname:''
}

},

created(){
this.setUserInfoFun().then(()=>{
this.getUserInfoFun()
})
},
methods:{
...mapMutations({
setUserInfo:'setUserInfo'
}),
setUserInfoFun(){
return new Promise((resolve,reject)=>{
let userInfo=JSON.parse(localStorage.getItem('shopdoc'));
this.setUserInfo(userInfo);
console.log(this.$store)
resolve()
})
},
getUserInfoFun(){
this.userImg=this.$store.state.userInfo.WXImgUrl
this.sname=this.$store.state.userInfo.shopName
this.wxname=this.$store.state.userInfo.wxName
},
logout(){
localStorage.removeItem("shopdoc");
cookie.remove('token');
location.reload();
}

},
components: {
XButton,
Group,
Cell
}

}



