
import {getRecomment,getDiscList} from 'api/recomment'
import axios from 'axios'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

export default {
data(){
return {
useId:'',
pwd:''
}

},
created(){

},
methods:{
loginReq(){
this.$vux.loading.show({
text: 'Loading'
})
axios.post('https://mp.orancat.com/oshop/login',{
userId:this.useId,
pwd:this.pwd
}
).then((res)=>{
this.$vux.loading.hide()
if(res.data.state==0){
cookie.set('token',res.data.shopdoc.token);
localStorage.setItem("shopdoc",JSON.stringify(res.data.shopdoc));
this.$store.state.bottomNav=true;
this.$router.push('/')
}else{
this.$vux.alert.show({
title:'提示',
content:res.data.message
})
}
})

}
},
components: {
XInput,
XButton
}
}



